//containerconnect( )
import { connect } from "react-redux";
import { toggleTodo } from "../actions/index";
import TodoList from "../components/TodoList";
import React from 'react';
const getVisibleTodos = (todos,filter) => {
  switch (filter){
    case "SHOW_ALL":
      return todos;
    case "SHOW_COMPLETED":
      return todos.filter(t =>{return t.completed});
    case "SHOW_ACTIVE":
      return todos.filter(t =>!t.completed);
    default:
      throw new Error('Unknown filter: ' + filter)
  }
}
//stateprops
const mapStateToProps = (state) =>{
  console.log(getVisibleTodos(state.todos,state.visibilityFilter));
  return{
    todos:getVisibleTodos(state.todos,state.visibilityFilter)     //statetodostodovisibilityFilter
  }
}
const mapDispatchToProps = (dispatch) =>{     //actions
  return{
    onTodoClick :(id) =>{
      dispatch(toggleTodo(id))
    }
  }
}
//toggleTodo( )actionsindex
// export const toggleTodo = (id) =>{
//   return{
//     type:"TOGGLE_TODO",
//     id
//   }
// }
//reactpropsstate
// dispatchmapStateToPropsprops;mapDispatchToProps:
//dispatchprops
const VisibleTodoList = connect(
  mapStateToProps,
  mapDispatchToProps
)(TodoList);
export default VisibleTodoList;
