import Todo from "./Todo";
import React from 'react';
//
const TodoList = ({todos,onTodoClick}) => (
  //
  <ul>
  {
    todos.map(todo =>

    <Todo key={todo.id}
          {...todo }                    //todoes6
          onClick={() => onTodoClick(todo.id)}
    />
  // onTodoClickcontainervisibleTodoListdispatchtoggleTodo
  //    return{
  //      type:"TOGGLE_TODO",
  //      id
  //      }
      )}
  </ul>

);
export default TodoList;
