// stateaction
// 
const todo = (state,action) =>{
  switch(action.type){
    case "ADD_TODO":
      //object;
      // alert(action.text);
      return{
        id:action.id,
        text:action.text,
        completed:false
      };
    case "TOGGLE_TODO":
      if(state.id !== action.id){
          return state
        }
    // assignstatecomplete
    //   return object.assign({},state,{completed: !state.completed})
      return {
        ...state,
        completed: !state.completed
      };
    default:
      return state
  }
};
//liststateobject,state
// todo
const todos = (state = [{
  id:1,
  text:111,
  completed:false
}],action) =>{
  switch(action.type){
    case "ADD_TODO":

      return[
        ...state,                    //todostate
         todo(undefined,action)     //object,,lists
      ];                            //actionstateundefined
     // console.log(state.concat([todo(undefined,action)]));
     //  return state.concat([todo(undefined,action)]);

    case "TOGGLE_TODO":
      return state.map(t=>todo(t,action))              //state;
    default:
      return state
  }
};
console.log(todos);
export default todos




